// src/app/providers/page.tsx
'use client';

import { Wifi, WifiOff, Settings, AlertCircle } from 'lucide-react';
import { Card } from '@/components/ui/Card';

export default function ProvidersPage() {
    const providers = [
        { id: '1', name: 'Main Gateway', status: 'online', uptime: 99.9, requests: 450000, location: 'US East' },
        { id: '2', name: 'Backup Gateway', status: 'online', uptime: 99.8, requests: 210000, location: 'US West' },
        { id: '3', name: 'Load Balancer', status: 'maintenance', uptime: 99.5, requests: 890000, location: 'EU Central' },
        { id: '4', name: 'SMS Gateway', status: 'offline', uptime: 95.2, requests: 0, location: 'Asia Pacific' },
    ];

    const getStatusIcon = (status: string) => {
        switch (status) {
            case 'online': return <Wifi className="h-5 w-5 text-green-500" />;
            case 'offline': return <WifiOff className="h-5 w-5 text-red-500" />;
            case 'maintenance': return <Settings className="h-5 w-5 text-amber-500" />;
            default: return <AlertCircle className="h-5 w-5 text-gray-500" />;
        }
    };

    const getStatusColor = (status: string) => {
        switch (status) {
            case 'online': return 'bg-green-100 text-green-800';
            case 'offline': return 'bg-red-100 text-red-800';
            case 'maintenance': return 'bg-amber-100 text-amber-800';
            default: return 'bg-gray-100 text-gray-800';
        }
    };

    return (
        <div className="space-y-6 animate-fade-in">
            <div className="flex items-center justify-between">
                <div>
                    <h1 className="text-3xl font-bold text-gray-900">Providers</h1>
                    <p className="text-gray-600 mt-2">Manage your USSD service providers and gateways</p>
                </div>
                <button className="px-4 py-2.5 bg-primary text-white font-medium rounded-xl hover:bg-primary/90 transition-colors shadow-lg shadow-primary/25">
                    Add Provider
                </button>
            </div>

            {/* Provider Status Overview */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-green-100 rounded-xl">
                            <Wifi className="h-6 w-6 text-green-600" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-gray-600">Online Providers</p>
                            <p className="text-2xl font-bold mt-1">2</p>
                        </div>
                    </div>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-red-100 rounded-xl">
                            <WifiOff className="h-6 w-6 text-red-600" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-gray-600">Offline Providers</p>
                            <p className="text-2xl font-bold mt-1">1</p>
                        </div>
                    </div>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-blue-100 rounded-xl">
                            <AlertCircle className="h-6 w-6 text-blue-600" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-gray-600">Total Requests</p>
                            <p className="text-2xl font-bold mt-1">1.55M</p>
                        </div>
                    </div>
                </Card>
            </div>

            {/* Providers List */}
            <Card>
                <div className="overflow-x-auto">
                    <table className="w-full">
                        <thead>
                            <tr className="border-b border-gray-200">
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Provider</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Status</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Uptime</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Requests</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Location</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Actions</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-100">
                            {providers.map((provider) => (
                                <tr key={provider.id} className="hover:bg-gray-50/50 transition-colors">
                                    <td className="py-4 px-4">
                                        <div className="flex items-center">
                                            {getStatusIcon(provider.status)}
                                            <span className="ml-3 font-medium text-gray-900">{provider.name}</span>
                                        </div>
                                    </td>
                                    <td className="py-4 px-4">
                                        <span className={`px-3 py-1 rounded-full text-xs font-medium ${getStatusColor(provider.status)}`}>
                                            {provider.status.charAt(0).toUpperCase() + provider.status.slice(1)}
                                        </span>
                                    </td>
                                    <td className="py-4 px-4">
                                        <div className="flex items-center">
                                            <div className="w-24 bg-gray-200 rounded-full h-2">
                                                <div
                                                    className={`h-2 rounded-full ${provider.uptime > 99 ? 'bg-green-500' :
                                                            provider.uptime > 95 ? 'bg-amber-500' : 'bg-red-500'
                                                        }`}
                                                    style={{ width: `${provider.uptime}%` }}
                                                ></div>
                                            </div>
                                            <span className="ml-3 text-sm font-medium text-gray-900">{provider.uptime}%</span>
                                        </div>
                                    </td>
                                    <td className="py-4 px-4">
                                        <span className="font-medium text-gray-900">{provider.requests.toLocaleString()}</span>
                                    </td>
                                    <td className="py-4 px-4">
                                        <span className="text-gray-600">{provider.location}</span>
                                    </td>
                                    <td className="py-4 px-4">
                                        <div className="flex space-x-2">
                                            <button className="p-2 text-gray-600 hover:text-primary hover:bg-primary/5 rounded-lg transition-colors">
                                                <Settings className="h-4 w-4" />
                                            </button>
                                            <button className="p-2 text-gray-600 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors">
                                                <WifiOff className="h-4 w-4" />
                                            </button>
                                        </div>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </Card>
        </div>
    );
}